import React from 'react'
import './fonts.css';
import {
    Avatar,
    Box,
    ButtonGroup,
    Card,
    CardContent,
    CardHeader, CardMedia, Container,
    CssBaseline, Divider,
    IconButton, Stack,
    Typography
} from "@mui/material";
import {createTheme, ThemeProvider} from '@mui/material/styles';
import TelegramIcon from '@mui/icons-material/Telegram';
import GitHubIcon from '@mui/icons-material/GitHub';
import YouTubeIcon from '@mui/icons-material/YouTube';
import gopVatar from './media/gop.gif'
import progGif from './media/prog.gif'

const theme = createTheme({
    palette: {
        mode: 'dark',
    },
});

class App extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            winHeight: window.innerHeight,
            winWidth: window.innerWidth,
            year: this.getData()
        }
        this.Logo = this.Logo.bind(this)
        this.GetStyled = this.GetStyled.bind(this)
    }


    componentDidMount() {
        window.addEventListener('resize', this.updateDimensions)
    }

    getData() {
        const datetime = new Date();
        return (datetime.getFullYear())
    }

    updateDimensions = () => {
        this.setState({winWidth: window.innerWidth, winHeight: window.innerHeight})
    }

    Logo() {
        return (
            <Box
                sx={{
                    marginTop: '30vh',
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                }}
            >
                <Typography variant="h3">
                    JEREZOFF
                </Typography>
                <ButtonGroup variant="contained">
                    <IconButton variant="outlined" href='https://github.com/jerezoff'><GitHubIcon/></IconButton>
                    <IconButton variant="outlined" href='https://t.me/jerezoff'><TelegramIcon/></IconButton>
                    <IconButton variant="outlined" href='https://www.youtube.com/channel/UCCidzUiJlyd_DFIEBteuL0A'><YouTubeIcon/></IconButton>
                </ButtonGroup>
            </Box>
        )
    }

    DevCard() {
        return (
            <Card>
                <CardHeader
                    avatar={
                        <Avatar alt="Jerezoff" src={gopVatar} aria-label="recipe">
                            j
                        </Avatar>
                    }
                    title="jerezoff"
                    subheader="Fullstack Programmer, IT Engineer"
                />
                <CardMedia
                    component="img"
                    height="194"
                    image={progGif}
                    alt="Paella dish"
                />
                <CardContent>
                        <Typography variant="h6">
                            Languages
                        </Typography>
                        <Typography variant="body1">
                            Java, JS, C#, etc...
                        </Typography>
                        <Typography variant="h6">
                            Libs
                        </Typography>
                        <Typography variant="body1">
                            Node, React, Electron, etc...
                        </Typography>
                </CardContent>
            </Card>
        )
    }

    VideoPreviewCard() {
        return (
            <Card>
                <CardHeader
                    avatar={
                        <Avatar alt="Jerezoff" src={gopVatar} aria-label="recipe">
                            j
                        </Avatar>
                    }
                    title="jerezoff"
                    subheader="Fullstack Programmer, IT Engineer"
                />
                <CardContent>
                    How are you guys? xd
                </CardContent>
            </Card>
        )
    }

    GetStyled() {
        return (
            <div style={{position: "relative", height: 'auto', maxWidth: this.state.winWidth}}>
                <ThemeProvider theme={theme}>
                    <CssBaseline/>
                    <this.Logo/>
                    <Container maxWidth="sm">
                        <Stack spacing={2}>
                            <this.DevCard/>
                            <this.VideoPreviewCard/>
                        </Stack>
                    </Container>
                    <Divider style={{marginTop: 10}} light/>
                    <Container style={{textAlign: 'center'}}>
                        <Stack spacing={2}>
                            <Typography variant='caption' align='center' style={{margin: 5}}>
                                Website created with <a href='https://reactjs.org/'>React</a> with <a
                                href={'https://mui.com/'}>MUI</a> God bless those guys for titanic work
                            </Typography>
                            <Typography variant='caption' style={{margin: 5}}>
                                2022-{this.state.year}
                            </Typography>
                        </Stack>
                    </Container>
                </ThemeProvider>
            </div>
        )
    }

    render() {
        return (
            <div>
                <this.GetStyled/>
            </div>
        );
    }

}

export default App;
